.fmargin{
  margin-top: 8px;
  margin-bottom: 8px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
}

.pload-wholediv{
  display: flex;
  flex-direction: column;
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.pload-div{
  display: flex;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  height: 200px;
  border-radius: 10px;

}
.left-content{
  display: flex;
  flex-direction: column;
  width: 100%;
}
.pload-text{
  width: 80%;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  border-radius: 10px;
}
.pload-button-div{
  display: flex;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  flex-wrap: wrap;
}
.pload-innerleft{
  display: flex;
  justify-content: left;
  width: 50%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.pload-innerright{
  display: flex;
  justify-content: right;
  width: 50%;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
} 

.pload-button{
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  width: 30%;
  height: 30px;
  border-radius: 10px;
  color: rgb(167, 167, 167);
}

@media  (max-width: 1139px) {
  .pload-wholediv{
    display: flex;
    width: 80%;
  }


}

@media  (max-width: 870px) {
  .pload-wholediv{
    display: flex;
    width: 80%;
  }


}
@media  (max-width: 600px) {

  .pload-wholediv{
    display: flex;
    width: 100%;
  }

}